.contact-page {
	@extend .service-page;
	padding: 60px 0;
	&:before {
		content: '';
		display: none; }
	.contact-map {
		@include bor-rad(5px);
		overflow: hidden;
		border: 1px solid darken($white,15%); }
	.contact-form {
		.module-title {
			margin-bottom: 10px;
			@include fs(14rem); }
		.form-wrap {
			input[type="submit"] {
				@include mainbutton;
				display: inline-block;
				line-height: 40px;
				border: none; } } } }
