.service-list {
	.service-col {
		margin-bottom: 20px;
		figure {
			.service-img {
				display: block;
				margin: 0 auto 30px;
				@include pos(rel,null);
				width: 180px;
				height: 180px;
				@include bor-rad(100px);
				overflow: hidden;
				box-shadow: 0px 10px 20px -10px color-rgba($dyellow,1);
				@include transition(all,1s);
				img {
					@include transition(all,0.5s); }
				.service-name {
					@include pos(abs, null 0 0 0);
					@include fs(20rem);
					// +font-title
					text-align: center;
					color: $white;
					font-weight: 700;
					padding: 15px 0;
					background: linear-gradient(color-rgba($lyellow,0),color-rgba($lyellow,1)); } }
			figcaption {
				max-width: 250px;
				text-align: center;
				margin: 0 auto;
				.service-des {
					@include fs(14rem);
					color: darken($white,60%);
					margin-bottom: 20px; }
				.link {
					@include fs(14rem);
					color: $dyellow;
					&:after {
						content: '\f178';
						margin-left: 10px;
 } } }						// +transition(all,0.3s)
			&:hover {
				.service-img {
					// +transform(rotateZ(360deg))
					box-shadow: 0px 10px 20px 0px color-rgba($dyellow,1);
					img {
						@include transform(scale3d(1.2,1.2,1)); } }
				figcaption {
					.link {} } } } } }
